                                {{item.message}}
                            </div>
                        
                        </li>
                    </ul>
                </van-list>

            </div>
        </div>
        
        <div class="input-box">
            <p class="line"></p>
            <div>
                <van-cell-group>
                    <van-field v-model="postParameter.message" placeholder="我也来说～" :border="false"   @keyup.enter="addPost" />
                </van-cell-group>
            </div>
        </div>
    </div>
</template>

<script>
import {getFromNom} from '@js/moment'
import { ImagePreview ,Toast} from 'vant';
export default {
  data() {
    return {
        loading:false,
        finished:false,
        data:null,
        commentData:[],
        name: this.$route.query.name,
        pageInfo:{
            page:0,
            size:10,
            count:0
        },
        postParameter:{
            token:this.$store.state.userData,
            tid:this.$route.query.tid,
            message:'',
            uppid:'',
            reply_pid:'',
            aid_list:'',
            attachment:0
        }
    }
  },
  created(){
      this.getSquaresItemDetail();
      this.getSquaresDetailComment();
  },
  methods: {
      onLoad(){
          if(this.pageInfo.page == Math.ceil(this.pageInfo.count/this.pageInfo.size)-1){
              this.finished = true
          }
          if(this.commentData.length){
              this.pageInfo.page++;
              this.getSquaresDetailComment();
          }else {
              
          }
      },
        ImagePreview(v,key){
        ImagePreview({
            images:v,
            startPosition:key
        })
        },
      getSquaresDetailComment(){
          this.api.getSquaresDetailComment({
              token:this.$store.state.userData,
              tid:this.$route.query.tid,
              page_index:this.pageInfo.page,
              page_size:this.pageInfo.size
          }).then(res => {
              this.loading = false;
              if(res.err_code == 0){
                  if(this.pageInfo.page == 0){
                      this.commentData = [];
                  }
                  if(!res.data.post.length){
                      this.finished = true
                  }
                  this.commentData = [...this.commentData,...res.data.post];
                  this.pageInfo.count = res.data.total
              }
          })
      }, 
        // 点赞或踩
        addZan(item,type){
        if(!this.$store.state.userData){
            window.location.href= "teldapp://login"
            return null;
        }
        let tid = item.tid,
            token = this.$store.state.userData;
        Toast.loading({
            message: '加载中...',
            forbidClick: true
        });    
        this.api.addZan({token,tid,type}).then(res => {
            Toast.clear();
            if(res.err_code == 0){
            if(item.is_zan == 1){
                if(type == 1){
                item.zan = parseInt(item.zan)-1
                item.is_zan = 0;
                }else if(type == 2){
                item.is_zan = type;
                item.zan = parseInt(item.zan)-1
                item.cai = parseInt(item.cai)+1
                }
            }else if(item.is_zan == 2){
                if(type == 1){
                    item.is_zan = type;
                    item.zan = parseInt(item.zan)+1
                    item.cai = parseInt(item.cai)-1
                }else if(type == 2){
                    item.is_zan = 0;
                    item.cai = parseInt(item.cai)-1
                }     
            }else {
                if(type == 1){
                item.zan = parseInt(item.zan)+1

                }else if(type == 2){
                item.cai = parseInt(item.cai)+1
                }
                item.is_zan = type;
            }
            if(item.is_zan){

            }
            
           // Toast(res.data.msg)
            }else {
            Toast(res.err_msg)
            }
        })
        },      
      // 回复
      addPost(){
            if(!this.$store.state.userData){
                window.location.href= "teldapp://login"
                return null;
            }
          this.api.addPost(this.postParameter).then( res => {
              if(res.err_code == 0){
                  this.postParameter.message = '';
                  this.pageInfo.page = 0;
                  this.getSquaresItemDetail();
                  this.getSquaresDetailComment();
              }
          })
      },  
      getSquaresItemDetail(){
          this.api.getSquaresItemDetail({
              token:this.$store.state.userData,
              tid:this.$route.query.tid
          }).then( res => {
              if(res.err_code == 0){
                  this.data = res.data.thread
              }
          } )
      },
      onClickLeft(){
        this.$router.go(-1)
      },
      onClickRight(){

      }
  },
  filters:{
    timeformat(v){
      if(v){
        return getFromNom(v*1000)
      }else {
        return ''
      }
    }
  }
}

</script>
